@charset "utf-8";

// Our variables
$base-font-family: Open Sans, sans-serif;
$monospace-font-family: Monaco, Menlo, Consolas, "Courier New", DotumChe, monospace;
$base-font-size:   16px;
$base-font-weight: 400;
$h1-weight: 700;
$h2-weight: 700;
$h3-weight: 400;
$small-font-size:  $base-font-size * 0.875;
$base-line-height: 1.5;

$spacing-unit:     30px;

$text-color:       #111;
$background-color: #fdfdfd;
$brand-color:      #2869e6;

$grey-color:       #757575;
$grey-color-light: lighten($grey-color, 45%);
$grey-color-dark:  darken($grey-color, 20%);

// Width of the content area
$content-width:    800px;

$on-palm:          600px;
$on-laptop:        800px;



// whiteglass also includes a mixin for defining media queries.
// Use media queries like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}


// custom code for contact container
#contact-container {
  clear: both;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1em;

  .pic {
    float: left;
  };

  .info {
    float: left;
    margin-left: 2em;
    font-family: Lato; 

    #email-small {
      display: none;
    }

    #address {
      display: block;
    }

    @include media-query($on-palm) {
      font-family: Lato; 

      p {
        font-size: 13px;
      }

      #email-small {
        display: block;
      }

      #address {
        display: none;
      }
    }

  };
}


// custom code for icons container
#icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 14px;

}

#icon-container-item {
  width: 32px;
  height: 32px;
}



// Import partials from the `whiteglass` theme.
@import "whiteglass";
@import "common/components/publications";
